import { ref, onMounted, onUnmounted } from 'vue'

export default function useLayoutConfig(){
  // ! `Android` is a javascript interface
  // ! manually unquote to invoke `Android`
  const text = `Android.getLayoutConfig()`
  
  const layoutConfig = ref([])
  try {
    layoutConfig.value = JSON.parse(text);
  } catch (error) {
    console.error('Error parsing JSON data:', error);
  }

  const updateLayoutConfig = () => {
    // ... some code
  }

  onMounted(() => {
    window.addEventListener('resize', updateLayoutConfig)
    updateLayoutConfig()
  })
  onUnmounted(() => {
    window.removeEventListener('resize', updateLayoutConfig)
  })

  return layoutConfig
}