<template>
  <div>
    <input class="border content left" type="text" :value="text" />
    <button @click="handle" class="right button">Submit</button>
  </div>
</template>

<script setup>
import { defineProps, ref } from 'vue';
import sendEvent from '../utils/SendEvent';

const props = defineProps({
  id: String,
  state_str: String,
  type: String,
  alt: String,
  text: String,
})

const text = ref(props.text);

function handle() {
  const data = {
    id: props.id,
    state_str: props.state_str,
    type: props.type,
    alt: props.alt,
    text: text.value
  }
  sendEvent(data)
}
</script>

<style scoped>
div {
  display: flex;
  align-items: center;
}
</style>