<template>
  <div>
    <button @click="scrollUp" class="button" style="width: 50%;"> Up </button>
    <button @click="scrollDown" class="button" style="width: 50%;">Down</button>
  </div>
</template>

<script setup>
import sendEvent from '../utils/SendEvent';
import { defineProps } from 'vue';

const props = defineProps({
  id: String,
  state_str: String,
  type: String,
  alt: String,
})

function scrollUp() {
  const data = {
    id: props.id,
    state_str: props.state_str,
    type: props.type,
    alt: props.alt,
    direction: 0,//up
  }
  sendEvent(data);
}

function scrollDown() {
  const data = {
    id: props.id,
    state_str: props.state_str,
    type: props.type,
    alt: props.alt,
    direction: 1,//down
  }
  sendEvent(data);
}

</script>

<style scoped>
div {
  display: flex;
  align-items: center;
}
</style>

