<template>
  <div>
    <button @click="handle" class="right button" :style="{ backgroundColor: checked ? '#00ff00' : '#ff0000' }">{{ checked ? "Checked" : "Unchecked" }}</button>
    <span class="border content left">{{ text }}</span>
  </div>
</template>

<script setup>
import { defineProps, ref } from 'vue';
import sendEvent from '../utils/SendEvent';

const props = defineProps({
  id: String,
  state_str: String,
  type: String,
  alt: String,
  text: String,
  checked: Boolean
})

const checked = ref(props.checked)

const data = {
  id : props.id,
  state_str : props.state_str,
  type : props.type,
  alt : props.alt,
  text : props.text,
  checked : props.checked
}

function handle(){
  checked.value = !checked.value
  data.checked = checked.value
  sendEvent(data)
}
</script>

<style scoped>
div {
  display: flex;
  align-items: center;
}
</style>

